import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import TaxCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/tax/tax/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/taxes";

class CreateTax extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createTax } = this.props;
    createTax();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.tax.error) {
      notification("error", this.props.tax.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.tax.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeTax } = this.props;
    value.percentage = value.percentage / 100;
    storeTax(value);
  };

  render() {
    // console.log(this.props);

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="taxes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <TaxCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            tax={this.props.tax}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    tax: state.tax
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateTax);
