import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Divider, Row, Col , Alert, InputNumber } from "antd";

const { Option } = Select;
const InputGroup = Input.Group;

class TaxCreateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { tax } = this.props;
    const { loading } = tax;
    const data = tax.tax ? tax.tax : [];
    const used = data.is_used;
    const error = tax.errorField ? tax.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "100%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="taxes.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="taxes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.add.name" />}
              validateStatus={
                error.tax_name || getFieldError("tax_name")
                  ? "error"
                  : null
              }
              help={
                error.tax_name
                  ? error.tax_name
                  : getFieldError("tax_name")
              }
            >
              {getFieldDecorator("tax_name", {
                initialValue: data.tax_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(
                <Input
                  //disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            
            <InputGroup>
              <Col span={6}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="taxes.add.percentage" />}
                >
                  {getFieldDecorator("percentage", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                    // disabled={used ? true : false}
                      min={0}
                      max={100}
                      precision={2}
                      formatter={value => `${value}%`}
                      parser={value => value.replace('%', '')}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={2} />
              <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="taxes.add.defaultSale" />}
                >
                  {getFieldDecorator("default_sale", {
                    initialValue: 0,
                  })(
                    <Radio.Group 
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="taxes.add.defaultPurchase" />}
                >
                  {getFieldDecorator("default_purchase", {
                    initialValue: 0,
                  })(
                    <Radio.Group 
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
          </div>
        </div>
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="taxes.add.subtitle.accounts" />
        </Divider>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
          <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.add.salesAccount" />}
            >
              {getFieldDecorator("id_account_sale", {
                initialValue: data.id_account_sale,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={tax.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.tax.accounts.map((data, key) => (
                    data.id_account_type === "10" 
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.add.purchaseAccount" />}
            >
              {getFieldDecorator("id_account_purchase", {
                initialValue: data.id_account_purchase,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={tax.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.tax.accounts.map((data, key) => (
                    data.id_account_type === "05"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>
        

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedTaxCreateForm = Form.create({ name: "create_tax" })(TaxCreateForm);

export default WrappedTaxCreateForm;
